footer {
	padding: 30px 8% 130px 8%;
	display: flex;
	flex-direction: column-reverse;
	background-color: #000;
	div {
		ul {
			display: flex;
			li {
				position: relative;
				&:first-child {
					margin-right: 10px;
					padding-right: 10px;
					&::after {
						content: "";
						position: absolute;
						top: 6px;
						right: 0;
						width: 2px;
						height: 14px;
						background-color: #ffffff; } }
				a {
					display: block;
					color: #ffffff;
					font-size: 14px;
					letter-spacing: -0.28px;
					line-height: 20px;
					font-weight: bold; } } }
		div {
			margin-top: 5px;
			padding-right: 150px;
			address {
				font-size: 12px;
				letter-spacing: -0.24px;
				line-height: 17px;
				color: #c9c9c9; } }
		p {
			margin-top: 50px;
			font-weight: bold;
			font-size: 16px;
			letter-spacing: -0.32px;
			line-height: 25px; } }
	.fooLogo {
		margin-bottom: 10px; } }
